import React from 'react';
import PropTypes from 'prop-types';
import { get } from 'lodash';
import { HeaderNavigation, HeaderMenuItem } from 'carbon-components-react';
import { Login24, Logout16, ChevronDown16 } from '@carbon/icons-react';
import { settings } from 'carbon-components';

import HeaderMenu from 'common/ui-shell/HeaderMenu';
import withAuth from 'common/auth/withAuth';

import './LoginButtonWidget.scss';

const { prefix } = settings;

const ButtonKeycloakLogic = ({
  applicationBaseURL,
  sessionUser,
  userDisplayName,
  currentPage,
  auth,
  authenticated,
}) => {

  const displayName = userDisplayName || get(auth, 'idTokenParsed.preferred_username');

  const handleLogin = () => auth.login({ redirectUri: currentPage });
  
  const handleLogout = () => auth.logout({ redirectUri: currentPage });

  return (
    <div className="LoginButtonWidget">
      {authenticated ? (
        <div className="LoginButtonWidget__wellcome">
          <HeaderNavigation aria-label="My Account">
            <HeaderMenu
              aria-label={displayName}
              menuLinkName={displayName}
              renderMenuContent={() => <ChevronDown16 className={`${prefix}--header__menu-arrow`} />}
              className="navigationMenu"
            >
              <HeaderMenuItem href={`${applicationBaseURL}do/main`}>
                Administration
              </HeaderMenuItem>
              <HeaderMenuItem onClick={handleLogout}>
                <Logout16 className="LoginButtonWidget__logoutsvg" /> Logout
              </HeaderMenuItem>
            </HeaderMenu>
          </HeaderNavigation>
        </div>
      ) : (
        <Login24 onClick={handleLogin} className="LoginButtonWidget__loginsvg" />
      )}
    </div>
  )

}

ButtonKeycloakLogic.propTypes = {
  applicationBaseURL: PropTypes.string.isRequired,
  sessionUser: PropTypes.string,
  authenticated: PropTypes.bool,
  auth: PropTypes.shape({}).isRequired,
  userDisplayName: PropTypes.string,
  currentPage: PropTypes.string,
};

ButtonKeycloakLogic.defaultProps = {
  sessionUser: '',
  userDisplayName: '',
  currentPage: '',
  authenticated: false,
};


export default withAuth(ButtonKeycloakLogic);
