import React, { Component } from 'react';
import { get } from 'lodash';
import PropTypes from 'prop-types';
import Keycloak from 'keycloak-js';
import { KeycloakProvider } from 'react-keycloak';
import ButtonKeycloakLogic from './ButtonKeycloakLogic';

class LoginButtonWidget extends Component {
  constructor(props) {
    super(props);
    this.state = {
      keycloak: null,
    };
    this.kcOnEvent = this.kcOnEvent.bind(this);
  }

  componentDidMount() {
    const { applicationBaseURL } = this.props;    
    const keycloak = new Keycloak(`${applicationBaseURL}keycloak.json`);
    this.setState({ keycloak });
  }

  setLocalKeys(username, token) {
    localStorage.setItem('username', username);
    localStorage.setItem('token', token);
  }
  
  unsetLocalKeys() {
    localStorage.removeItem('username');
    localStorage.removeItem('token');
  }

  kcOnEvent(event) {
    const { keycloak } = this.state;
    const username = get(keycloak, 'idTokenParsed.preferred_username');
    const { token } = keycloak;
    switch (event) {
      case 'onAuthSuccess':
        this.setLocalKeys(username, token);
        break;
      case 'onAuthRefreshSuccess':
        this.setLocalKeys(username, token);
        break;
      case 'onAuthRefreshError':
        this.unsetLocalKeys();
        keycloak.logout();
        break;
      case 'onAuthLogout':
        this.unsetLocalKeys();
        break;
      default:
        break;
    }
  }

  render() {
    const { keycloak } = this.state;
    const config = { onLoad: 'check-sso', checkLoginIframe: true };
    if (!keycloak) {
      return null;
    }
    return (
      <KeycloakProvider keycloak={keycloak} initConfig={config} onEvent={this.kcOnEvent}>
        <ButtonKeycloakLogic {...this.props} />
      </KeycloakProvider>
    );
  }
}

LoginButtonWidget.propTypes = {
  applicationBaseURL: PropTypes.string,
};

LoginButtonWidget.defaultProps = {
  applicationBaseURL: process.env.REACT_APP_BASEURL,
};

export default LoginButtonWidget;
